export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {}
		};
	},
	onShow() {
		var me = this;
		// 使用挂载方法获取用户数据
		var userInfo = me.getGlobalUser("globalUser");
		if (userInfo != null) {
			me.userIsLogin = true;
			me.userInfo = userInfo;
		} else {
			me.userIsLogin = false;
			me.userInfo = {};
		}
	}
}
